body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-notification--outlined .f-notification__inner {
  border: 2px solid silver;
  border-radius: 0.25em;
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  padding-left: 1em;
  padding-right: 1em; }

.f-notification--outlined.f-notification--alert .f-notification__inner {
  border-color: #e91e63;
  background-color: rgba(233, 30, 99, 0.1); }

.f-notification--outlined.f-notification--alert .f-notification__text {
  color: #e91e63; }

.f-notification--outlined.f-notification--alert .f-notification__icon {
  color: #e91e63; }

.f-notification--outlined.f-notification--success .f-notification__inner {
  border-color: #cddc39;
  background-color: rgba(205, 220, 57, 0.1); }

.f-notification--outlined.f-notification--success .f-notification__text {
  color: #cddc39; }

.f-notification--outlined.f-notification--success .f-notification__icon {
  color: #cddc39; }
